<template>
    <Section>
        <template #title>
            
            <HelpIcon :help="panel_method_help" />
        </template>
        <template #content>
            <Field label="">
                <Select v-model="store.settings." :options="['AI', '']" />
            </Field>

            <template v-if="store.settings. === ''">
                <div class="mvu-subtitle">
                    <span></span>
                    <HelpIcon :help="panel_extra_mode_help" />
                </div>

                <Checkbox v-model="store.settings..">
                    <span></span>
                </Checkbox>

                <Checkbox v-model="store.settings..">
                    <span></span>
                </Checkbox>

                <Checkbox v-model="store.settings.">
                    <span></span>
                    <HelpIcon :help="auto_analyze_help" />
                </Checkbox>

                <Field label="">
                    <Select
                        v-model="store.settings.."
                        :options="['', '']"
                    />
                </Field>

                <template v-if="store.settings.. === ''">
                    <div class="mvu-subtitle"> API</div>

                    <div class="mvu-field-grid">
                        <Field label="API ">
                            <input
                                v-model="store.settings..api"
                                type="text"
                                class="text_pole"
                                placeholder="http://localhost:1234/v1"
                            />
                        </Field>

                        <Field label="API ">
                            <input
                                v-model="store.settings.."
                                type="password"
                                class="text_pole"
                                placeholder=""
                            />
                        </Field>

                        <Field label="">
                            <ModelSelect />
                        </Field>
                    </div>

                    <details class="mvu-details">
                        <summary class="mvu-details__summary"></summary>
                        <div class="mvu-details__content">
                            <div v-if="!additional_extra_configuration_supported" class="mvu-note">
                                
                            </div>

                            <div class="mvu-advanced-grid">
                                <Field label=" token">
                                    <input
                                        v-model.number="
                                            store.settings..token
                                        "
                                        :disabled="!additional_extra_configuration_supported"
                                        type="number"
                                        class="text_pole"
                                        min="0"
                                        step="128"
                                        placeholder="4096"
                                    />
                                </Field>

                                <Field label="">
                                    <RangeNumber
                                        v-model="store.settings.."
                                        :disabled="!additional_extra_configuration_supported"
                                        :min="0"
                                        :max="2"
                                        :step="0.01"
                                    />
                                </Field>

                                <Field label="Top P">
                                    <RangeNumber
                                        v-model="store.settings..top_p"
                                        :disabled="!additional_extra_configuration_supported"
                                        :min="0"
                                        :max="1"
                                        :step="0.01"
                                    />
                                </Field>

                                <Field label="">
                                    <RangeNumber
                                        v-model="store.settings.."
                                        :disabled="!additional_extra_configuration_supported"
                                        :min="-2"
                                        :max="2"
                                        :step="0.01"
                                    />
                                </Field>

                                <Field label="">
                                    <RangeNumber
                                        v-model="store.settings.."
                                        :disabled="!additional_extra_configuration_supported"
                                        :min="-2"
                                        :max="2"
                                        :step="0.01"
                                    />
                                </Field>
                            </div>
                        </div>
                    </details>
                </template>
            </template>
        </template>
    </Section>
</template>

<script setup lang="ts">
import Checkbox from '@/panel/component/Checkbox.vue';
import Field from '@/panel/component/Field.vue';
import HelpIcon from '@/panel/component/HelpIcon.vue';
import ModelSelect from '@/panel/component/ModelSelect.vue';
import RangeNumber from '@/panel/component/RangeNumber.vue';
import Section from '@/panel/component/Section.vue';
import Select from '@/panel/component/Select.vue';
import auto_analyze_help from '@/panel/help/auto_analyze.md';
import panel_extra_mode_help from '@/panel/help/extra_mode.md';
import panel_method_help from '@/panel/help/update_method.md';
import { useSettingsStore } from '@/settings';
import { getSillyTavernVersion, getTavernHelperVersion } from '@/util';
import { compare } from 'compare-versions';
import { watch } from 'vue';

const additional_extra_configuration_supported = compare(getTavernHelperVersion(), '4.0.14', '>=');

const store = useSettingsStore();

watch(
    () => store.settings.,
    value => {
        if (value === '' && compare(getSillyTavernVersion(), '1.13.4', '<')) {
            toastr.error(
                "'' 1.13.4",
                "[MVU]''",
                { timeOut: 5000 }
            );
            store.settings. = 'AI';
        }
    }
);

watch(
    () => store.settings..,
    value => {
        if (value === true) {
            if (!SillyTavern.ToolManager.isToolCallingSupported()) {
                toastr.error(
                    " API  () ''",
                    "[MVU]''",
                    {
                        timeOut: 5000,
                    }
                );
            }
            if (SillyTavern.chatCompletionSettings.function_calling === false) {
                toastr.error("''", "[MVU]''", {
                    timeOut: 5000,
                });
            }
            store.settings.. = true;
        }
    }
);
</script>

